import { types, Instance } from 'mobx-state-tree';

import { Platform } from '@/shared/lib/index.js';

import { LayoutName } from '../lib/layout/types/Layout.js';

export const Settings = types
    .model('Settings', {
        layout: types.maybeNull(types.enumeration(Object.keys(LayoutName))),
        clientTld: types.string,
        platform: types.maybe(types.enumeration(Object.values(Platform)))
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isDesktopApplication() {
            return Boolean(self.platform);
        }
    }))
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            setLayout(layout: keyof typeof LayoutName) {
                self.layout = layout;
            },

            setClientTld(tld: string) {
                self.clientTld = tld;
            },

            setPlatform(platform: Platform) {
                self.platform = platform;
            }
        };
    });

export type SettingsModel = Instance<typeof Settings>;
