import { types, Instance, flow, getRoot } from 'mobx-state-tree';

import { UsersResourceLibraryResponse } from '@yandex-music-int/resource-collection';

import { buildUrl } from '@/shared/lib/index.js';
import { LikesCount } from '@/shared/models/index.js';

import { PlaylistType } from '../lib/types/PlaylistType.js';

export const Playlist = LikesCount.props({
    uuid: types.string,
    available: types.boolean,
    uid: types.number,
    kind: types.number,
    title: types.maybe(types.string),
    coverUri: types.maybe(types.string),
    likesCount: types.maybe(types.number),
    averageColor: types.maybe(types.string)
})
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get key() {
            return `${self.uuid}_${self.uid}_${self.kind}`;
        },
        get url() {
            return buildUrl('/playlist/:playlistUuid', { playlistUuid: self.uuid });
        },
        get isFavouritePlaylist() {
            // Является ли плейлистом "Мне нравится". В процессе перехода на uuid
            // было бы круто завести на бэкенде специальное поле
            return self.kind === PlaylistType.LIKE;
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isLiked() {
            // @ts-ignore
            const { library } = getRoot(self);
            // Library пока работат по старой схеме uid+kind
            return library.isPlaylistLiked(`${self.uid}:${self.kind}`);
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => ({
        toggleLike: flow(function* () {
            // @ts-ignore
            const { library, user } = getRoot(self);

            if (user.isAuthorized) {
                const result = yield library.togglePlaylistLike({
                    userId: user.account.uid,
                    // Library пока работат по старой схеме uid+kind
                    entityId: `${self.uid}:${self.kind}`,
                    ownerId: self.uid,
                    kindId: self.kind
                });

                if (result === UsersResourceLibraryResponse.OK) {
                    if (self.isLiked) {
                        self.likePending();
                    } else {
                        self.unlikePending();
                    }
                }

                return result;
            }
        })
    }));

export type PlaylistModel = Instance<typeof Playlist>;
