import { ReactElement } from 'react';

import { BaseNotificationLike } from '@/shared/ui/index.js';

import { AlbumModel } from '../model/Album.js';

interface Props {
    album: AlbumModel;
    closeToast?: () => void;
}

export const NotificationLike = ({ album, closeToast }: Props): ReactElement => {
    return (
        <BaseNotificationLike
            onClose={closeToast}
            entityVariant="album"
            coverUri={album.coverUri}
            entityUrl={`/album/${album.id}`}
            collectionUrl="/collection/albums"
            entityTitle={album.title}
            isLiked={album.isLiked}
        />
    );
};
