'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, ReactNode, useCallback } from 'react';

import { YaMusicContextData } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { WithClassName } from '@/shared/lib/index.js';

import { ChartTrackWithArtistsModel } from '../../../model/ChartTrackWithArtists.js';
import { Chart } from '../../Cells/Chart/Chart/Chart.js';
import { ControlsBar } from '../../Cells/Controls/ControlsBar/ControlsBar.js';
import { ColumnMeta } from '../../Cells/Meta/ColumnMeta/ColumnMeta.js';
import { PlayButtonWithCover } from '../../Cells/PlayButton/PlayButtonWithCover/PlayButtonWithCover.js';
import { ExternalPlayButtonCellProps } from '../../Cells/PlayButton/types/PlayButtonCellProps.js';
import { Track } from '../../Track/Track.js';

import styles from './TrackChart.module.scss';

interface Props extends WithClassName {
    track: ChartTrackWithArtistsModel;
    playContextParams: PlayContextParams<YaMusicContextData, MusicEntityData>;
}

export const TrackChart = observer(({ track, playContextParams }: Props): ReactElement => {
    const playButtonCellRender = useCallback(
        (props: ExternalPlayButtonCellProps): ReactNode => {
            return <PlayButtonWithCover track={track} className={styles.playButtonCell} {...props} />;
        },
        [track]
    );

    return (
        <Track
            track={track}
            meta={<ColumnMeta track={track} />}
            chart={<Chart track={track} className={styles.chartCell} />}
            playButtonCellRender={playButtonCellRender}
            controls={<ControlsBar track={track} className={styles.controlsBarCell} />}
            playContextParams={playContextParams}
        />
    );
});
