// Проверяет, есть ли в списке (плейлисте) дубликаты трека до элемента index

export const hasDuplicatesBefore = <T extends { id: string }>(list: T[] = [], item: T, index: number): boolean => {
    const firstDuplicateIndex = list.findIndex((track: T) => track.id === item.id);

    return index > firstDuplicateIndex;
};
