'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import React, { MouseEvent, ReactElement, useCallback, useMemo, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { Button } from '@yandex-music-int/sol/components/Button';
import { EntityCardProps, EntityCard, CommonCardControls } from '@yandex-music-int/sol/components/EntityCard';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Label } from '@yandex-music-int/sol/components/Label';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { SeparatedArtists } from '@/entities/artist/index.js';

import {
    useSearchFeedback,
    additionFormatter,
    WithChildren,
    WithClassName,
    usePlayableContext,
    useCreateFrom
} from '@/shared/lib/index.js';
import { LikeButton, PlayButton, useFuture } from '@/shared/ui/index.js';

import { useOnLikeClick } from '../../lib/useOnLikeClick.js';
import { TrackWithArtistsModel } from '../../model/TrackWithArtists.js';
import { UgcTrackWithArtistsModel } from '../../model/UgcTrackWithArtists.js';

import styles from './TrackCard.module.scss';

interface Props extends WithClassName, WithChildren {
    track: TrackWithArtistsModel | UgcTrackWithArtistsModel;
    contentLinesCount?: EntityCardProps['contentLinesCount'];
}

export const TrackCard = observer(({ className, children, track, contentLinesCount }: Props): ReactElement => {
    const from = useCreateFrom();
    const { formatMessage } = useIntl();
    const { isFuture } = useFuture();
    const handleOnLikeClick = useOnLikeClick(track);

    const trackName = useMemo(() => {
        return formatMessage({ id: 'entity-names.track-name' }, { trackName: track.title });
    }, [formatMessage, track.title]);

    const addition = additionFormatter([track.version]);

    //TODO: заменить на внутреннюю обработку в MenuButton
    const handleClick = useCallback((event: MouseEvent<HTMLButtonElement>) => {
        event.stopPropagation();
        event.preventDefault();
    }, []);

    const { isPlaying, togglePlay } = usePlayableContext({
        playContextParams: {
            contextData: {
                type: YaMusicContexts.Various,
                meta: {
                    id: track.id
                },
                from
            },
            loadContextMeta: true
        },
        entityId: track.id
    });

    const { sendLikeSearchFeedback, sendPlaySearchFeedback } = useSearchFeedback();

    const [onSearchFeedbackPlayed, setOnSearchFeedbackPlayed] = useState(false);
    const handlePlayButtonClick = useCallback(() => {
        if (!onSearchFeedbackPlayed && !isPlaying) {
            setOnSearchFeedbackPlayed(true);
            sendPlaySearchFeedback?.();
        }

        togglePlay();
    }, [isPlaying, onSearchFeedbackPlayed, sendPlaySearchFeedback, togglePlay]);

    const [onSearchFeedbackLiked, setOnSearchFeedbackLiked] = useState(false);
    const handleLikeButtonClick = useCallback(() => {
        if (!onSearchFeedbackLiked && !track.isLiked) {
            setOnSearchFeedbackLiked(true);
            sendLikeSearchFeedback?.();
        }

        handleOnLikeClick();
    }, [handleOnLikeClick, onSearchFeedbackLiked, sendLikeSearchFeedback, track.isLiked]);

    const menuControl = useMemo(() => {
        if (isFuture) {
            return (
                <Button
                    className={styles.icon}
                    //TODO: Заменить на релевантное имя, когда будет готово контекстное меню
                    aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                    icon={<Icon size="xxs" variant="more" />}
                    size="s"
                    radius="round"
                    onClick={handleClick}
                />
            );
        }

        return null;
    }, [isFuture, formatMessage, handleClick]);

    const view = useMemo(() => {
        return (
            <Paper className={styles.cover} radius="l" withShadow>
                <div className={styles.coverLink} aria-label={track.title}>
                    <Image
                        className={styles.image}
                        src={track.coverUri}
                        size={200}
                        fit="cover"
                        alt={trackName}
                        withAvatarReplace
                    />
                    <Label className={styles.trackLabel}>
                        <FormattedMessage id="entity-names.track" />
                    </Label>
                    <CommonCardControls
                        className={styles.controls}
                        playControl={
                            <PlayButton
                                className={styles.playButton}
                                buttonVariant="default"
                                withHover={false}
                                iconSize="l"
                                variant="filled"
                                isPlaying={isPlaying}
                                onClick={handlePlayButtonClick}
                                disabled={!track.isAvailable}
                            />
                        }
                        likeControl={
                            <LikeButton
                                isLiked={track.isLiked}
                                onClick={handleLikeButtonClick}
                                variant="default"
                                size="s"
                                iconSize="xxs"
                            />
                        }
                        menuControl={menuControl}
                    />
                </div>
            </Paper>
        );
    }, [
        handleLikeButtonClick,
        handlePlayButtonClick,
        isPlaying,
        menuControl,
        track.coverUri,
        track.isAvailable,
        track.isLiked,
        track.title,
        trackName
    ]);

    return (
        <EntityCard
            className={clsx(styles.root, className)}
            aria-label={trackName}
            explicitMark={track.explicitMarkVariant}
            title={
                <Caption
                    className={styles.titleLink}
                    variant="div"
                    type="entity"
                    size="s"
                    weight="medium"
                    lineClamp={2}
                >
                    {track.title}
                </Caption>
            }
            contentLinesCount={contentLinesCount}
            view={view}
            description={
                <SeparatedArtists
                    className={styles.artists}
                    artists={track.artists}
                    lineClamp={1}
                    linkClassName={styles.artistLink}
                    captionSize="s"
                    withLink={!track.isUGC}
                />
            }
        >
            {addition && (
                <Caption className={styles.addition} variant="div" type="entity" size="s" weight="medium" lineClamp={1}>
                    {addition}
                </Caption>
            )}
            {children}
        </EntityCard>
    );
});
