'use client';

import { useCallback, useState } from 'react';
import { useIntl } from 'react-intl';

import { UsersResourceLibraryResponse } from '@yandex-music-int/resource-collection';

import { NotificationLike } from '@/entities/track/index.js';

import { NotificationContainerId, useNotify, useStore } from '@/shared/lib/index.js';
import { BaseNotificationError } from '@/shared/ui/index.js';

import { TrackWithArtistsModel } from '../model/TrackWithArtists.js';
import { UgcTrackWithArtistsModel } from '../model/UgcTrackWithArtists.js';

export const useOnLikeClick = (track: TrackWithArtistsModel | UgcTrackWithArtistsModel): (() => Promise<void>) => {
    const { user } = useStore();
    const { notify } = useNotify();
    const [pendingLibraryAction, setPendingLibraryAction] = useState(false);
    const { formatMessage } = useIntl();

    return useCallback(async () => {
        if (!user.isAuthorized) {
            notify(
                <BaseNotificationError error={formatMessage({ id: 'authorization-messages.need-to-authorizate' })} />,
                {
                    containerId: NotificationContainerId.ERROR
                }
            );
            return;
        }

        if (pendingLibraryAction) {
            return;
        }

        setPendingLibraryAction(true);

        const result: UsersResourceLibraryResponse = await track.toggleLike();

        setPendingLibraryAction(false);

        if (result === UsersResourceLibraryResponse.OK) {
            notify(<NotificationLike track={track} />, { containerId: NotificationContainerId.INFO });
        } else {
            notify(<BaseNotificationError error={formatMessage({ id: 'error-messages.error-during-action' })} />, {
                containerId: NotificationContainerId.ERROR
            });
        }
    }, [user.isAuthorized, pendingLibraryAction, track, notify, formatMessage]);
};
