'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactNode, ReactElement, MouseEvent, useCallback, useState } from 'react';

import { YaMusicContextData } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { createRipple } from '@yandex-music-int/sol/common/utils/createRipple.js';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { useSearchFeedback, WithClassName, usePlayableContext } from '@/shared/lib/index.js';

import { TrackWithArtistsModel } from '../../model/TrackWithArtists.js';
import { ExternalPlayButtonCellProps } from '../Cells/PlayButton/types/PlayButtonCellProps.js';

import styles from './Track.module.scss';

const DOUBLE_CLICK_EVENT = 2;

export interface Props extends WithClassName {
    track: TrackWithArtistsModel;
    meta: ReactElement;
    chart?: ReactElement;
    controls?: ReactElement;

    chartCellRender?: () => ReactNode;
    playButtonCellRender?: (props: ExternalPlayButtonCellProps) => ReactNode;
    playContextParams: PlayContextParams<YaMusicContextData, MusicEntityData>;

    // Должен ли трек, помимо своего entityId, проверять свою позицию в текущем списке для определения статуса проигрывания
    shouldCheckSelfIndex?: boolean;
    // Если в списке есть дубликаты текущего трека, то является ли текущий трек первым дубликатом?
    hasDuplicatesBefore?: boolean;
}

export const Track = observer(
    ({
        className,
        track,
        meta,
        chart,
        controls,
        playButtonCellRender,
        playContextParams,
        shouldCheckSelfIndex,
        hasDuplicatesBefore
    }: Props): ReactElement => {
        const { isPlaying, isCurrent, togglePlay, restartPlay } = usePlayableContext({
            playContextParams,
            entityId: track.id,
            shouldCheckSelfIndex,
            hasDuplicatesBefore
        });

        const { sendPlaySearchFeedback } = useSearchFeedback();

        const [onSearchFeedbackPlayed, setOnSearchFeedbackPlayed] = useState(false);
        const handlePlayButtonClick = useCallback(() => {
            if (!onSearchFeedbackPlayed && !isPlaying) {
                setOnSearchFeedbackPlayed(true);
                sendPlaySearchFeedback?.();
            }

            togglePlay();
        }, [isPlaying, onSearchFeedbackPlayed, sendPlaySearchFeedback, togglePlay]);

        const handleDoubleClick = useCallback(() => {
            if (isPlaying) {
                restartPlay();
            } else {
                handlePlayButtonClick();
            }
        }, [isPlaying, restartPlay, handlePlayButtonClick]);

        const handleClick = useCallback(
            (event: MouseEvent<HTMLDivElement>) => {
                if (track.isAvailable) {
                    if (event.detail === DOUBLE_CLICK_EVENT) {
                        handleDoubleClick();
                    } else {
                        createRipple(event, styles.ripple);
                    }
                }
            },
            [track.isAvailable, handleDoubleClick]
        );

        const playButtonCellComponent = playButtonCellRender?.({
            onPlayButtonClick: handlePlayButtonClick,
            isPlaying,
            isCurrent
        });

        return (
            <div
                className={clsx(styles.root, className, 'trackChildrenVisibility', {
                    [styles.root_disabled]: !track.isAvailable,
                    [styles.root_disliked]: track.isDisliked
                })}
                aria-label={`${track.artistsNames} ${track.title}`}
            >
                <Paper radius="s" className={styles.paper} onClick={handleClick}>
                    {chart}
                    {playButtonCellComponent}
                    {meta}
                    {controls}
                </Paper>
            </div>
        );
    }
);
