'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';
import { useIntl } from 'react-intl';

import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';

import { PlayButton } from '@/shared/ui/PlayButton/PlayButton.js';

import { PlayingAnimation } from '../PlayingAnimation/PlayingAnimation.js';
import { PlayButtonCellProps, ExternalPlayButtonCellProps } from '../types/PlayButtonCellProps.js';

import styles from './PlayButtonWithCover.module.scss';

export const PlayButtonWithCover = observer(
    ({
        className,
        track,
        onPlayButtonClick,
        isPlaying,
        isCurrent
    }: PlayButtonCellProps & ExternalPlayButtonCellProps): ReactElement => {
        const { formatMessage } = useIntl();

        return (
            <div
                className={clsx(className, styles.root, {
                    [styles.root_disabled]: !track.isAvailable,
                    [styles.root_current]: isCurrent,
                    [styles.root_disliked]: track.isDisliked,
                    [styles.root_playing]: isPlaying
                })}
            >
                <Paper className={styles.cover} radius="xs">
                    <Image
                        className={styles.coverImage}
                        src={track.coverUri}
                        size={100}
                        alt={formatMessage({ id: 'entity-names.track-name' }, { trackName: track.title })}
                        fit="cover"
                        withAvatarReplace
                    />
                    <div className={styles.control}>
                        {track.isAvailable && (
                            <>
                                <PlayingAnimation className={styles.playingAnimation} />

                                <PlayButton
                                    variant="filled"
                                    className={styles.playButton}
                                    iconClassName={styles.playButtonIcon}
                                    isPlaying={isPlaying}
                                    onClick={onPlayButtonClick}
                                    iconSize="xs"
                                />
                            </>
                        )}
                    </div>
                </Paper>
            </div>
        );
    }
);
