'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement } from 'react';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { PlayButton } from '@/shared/ui/PlayButton/PlayButton.js';

import { PlayingAnimation } from '../PlayingAnimation/PlayingAnimation.js';
import { PlayButtonCellProps, ExternalPlayButtonCellProps } from '../types/PlayButtonCellProps.js';

import styles from './PlayButtonWithPosition.module.scss';

export const PlayButtonWithPosition = observer(
    ({
        className,
        track,
        position,
        onPlayButtonClick,
        isPlaying,
        isCurrent
    }: PlayButtonCellProps & ExternalPlayButtonCellProps): ReactElement => {
        return (
            <div
                className={clsx(className, styles.root, {
                    [styles.root_disabled]: !track.isAvailable,
                    [styles.root_playing]: isPlaying,
                    [styles.root_disliked]: track.isDisliked,
                    [styles.root_current]: isCurrent
                })}
            >
                {track.isAvailable && (
                    <>
                        <PlayingAnimation className={styles.playingAnimation} />

                        <PlayButton
                            variant="filled"
                            className={styles.playButton}
                            iconClassName={styles.playButtonIcon}
                            isPlaying={isPlaying}
                            onClick={onPlayButtonClick}
                            iconSize="xs"
                        />
                    </>
                )}

                {position && (
                    <Caption variant="div" className={styles.position} weight="normal" type="entity" size="m">
                        {position}
                    </Caption>
                )}
            </div>
        );
    }
);
