import { AlbumType } from '@yandex-music-int/resource-collection';

export const getReleaseAlbumTitle = (type?: AlbumType): string => {
    switch (type) {
        case AlbumType.SINGLE:
            return 'entity-names.new-single';

        case AlbumType.PODCAST:
            return 'entity-names.new-podcast';

        default:
            return 'entity-names.new-album';
    }
};
