import { AlbumType } from '@yandex-music-int/resource-collection';

export const getAlbumTypeName = (type?: AlbumType): string => {
    switch (type) {
        case AlbumType.SINGLE:
            return 'entity-names.single';

        case AlbumType.PODCAST:
            return 'entity-names.podcast';

        default:
            return 'entity-names.album';
    }
};
