'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo, useState, useCallback } from 'react';
import { useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Menu, MenuItem, SubMenu } from '@yandex-music-int/sol/components/Menu';
import { Popover, PopoverContent } from '@yandex-music-int/sol/components/Popover';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { useSearchFeedback, LayoutName, WithClassName, useStore } from '@/shared/lib/index.js';
import { DislikeButton, LikeButton, useFuture } from '@/shared/ui/index.js';

import { getFormattedDuration } from '../../../../lib/getFormattedDuration.js';
import { useOnDislikeClick } from '../../../../lib/useOnDislikeClick.js';
import { useOnLikeClick } from '../../../../lib/useOnLikeClick.js';
import { TrackWithArtistsModel } from '../../../../model/TrackWithArtists.js';

import styles from './ControlsBar.module.scss';

interface Props extends WithClassName {
    track: TrackWithArtistsModel;
}

export const ControlsBar = observer(({ className, track }: Props): ReactElement => {
    const { isFuture } = useFuture();
    const { user, settings } = useStore();
    const { formatMessage } = useIntl();
    const handleOnLikeClick = useOnLikeClick(track);

    const { sendLikeSearchFeedback } = useSearchFeedback();

    const [onSearchFeedbackLiked, setOnSearchFeedbackLiked] = useState(false);
    const handleLikeButtonClick = useCallback(() => {
        if (!onSearchFeedbackLiked && !track.isLiked) {
            setOnSearchFeedbackLiked(true);
            sendLikeSearchFeedback?.();
        }

        handleOnLikeClick();
    }, [handleOnLikeClick, onSearchFeedbackLiked, sendLikeSearchFeedback, track.isLiked]);

    const isMobile = useMemo(() => {
        return settings.layout === LayoutName.Mobile;
    }, [settings.layout]);

    const formattedDuration = useMemo(() => getFormattedDuration(track.durationMs), [track.durationMs]);

    const contextMenu = useMemo(() => {
        return (
            <Popover placement="bottom" strategy="fixed" offsetOptions={10} isMobile={isMobile}>
                <Button
                    variant="text"
                    size="xs"
                    withRipple={false}
                    radius="round"
                    className={styles.contextMenu}
                    aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                    icon={<Icon variant="more" size="xxs" />}
                />
                <PopoverContent className={styles.popover}>
                    <Menu
                        withHeader={isMobile}
                        isMobile={isMobile}
                        title={track.title}
                        description="1 704 888 слушателей за месяц"
                    >
                        <MenuItem icon={<Icon variant="liked" />}>Мне нравится</MenuItem>
                        <SubMenu icon={<Icon variant="arrowRight" />} text="Добавить в плейлист">
                            <MenuItem>Плейлист 1</MenuItem>
                            <MenuItem>Плейлист 2</MenuItem>
                            <MenuItem>Плейлист 3</MenuItem>
                            <MenuItem>Плейлист 4</MenuItem>
                        </SubMenu>
                        <MenuItem icon={<Icon variant="dislike" />}>Мне не нравится</MenuItem>
                    </Menu>
                </PopoverContent>
            </Popover>
        );
    }, [formatMessage, isMobile, track.title]);

    return (
        <div
            className={clsx(styles.root, styles.controls, className, {
                [styles.controls_disliked]: track.isDisliked,
                [styles.controls_disabled]: !track.isAvailable
            })}
        >
            {user.isAuthorized && !track.isRemoved && (
                <LikeButton
                    size="xs"
                    iconSize="xxs"
                    className={clsx(styles.item, styles.likeIcon)}
                    isLiked={track.isLiked}
                    onClick={handleLikeButtonClick}
                />
            )}

            {user.isAuthorized && !track.isRemoved && (
                <DislikeButton
                    size="xs"
                    iconSize="xxs"
                    className={clsx(styles.item, styles.dislikeIcon)}
                    isDisliked={track.isDisliked}
                    onClick={useOnDislikeClick(track)}
                />
            )}

            <div className={clsx(styles.item, styles.contextMenuWrapper)}>
                {typeof track.durationMs === 'number' && (
                    <Caption variant="span" className={styles.duration} type="entity" size="m" weight="medium">
                        {formattedDuration}
                    </Caption>
                )}
                {isFuture && contextMenu}
            </div>
        </div>
    );
});
