import { types, Instance } from 'mobx-state-tree';

import { Artist, ArtistModel } from '@/entities/artist/index.js';
import { Track } from '@/entities/track/index.js';

export const TrackWithArtists = Track.props({
    artists: types.array(Artist)
})
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get artistsNames() {
            return self.artists?.map((artist: ArtistModel) => artist.name).join(', ');
        }
    }));

export type TrackWithArtistsModel = Instance<typeof TrackWithArtists>;
