import { cast } from 'mobx-state-tree';

import { Chart, Track } from '@yandex-music-int/resource-collection';

import { ChartTrackWithArtistsModel } from '../model/ChartTrackWithArtists.js';

import { trackWithArtistsResponseToModel } from './trackWithArtistsResponseToModel.js';

export const chartTrackWithArtistsResponseToModel = (track: Track, chart?: Chart): ChartTrackWithArtistsModel => {
    return cast({
        ...trackWithArtistsResponseToModel(track),
        position: chart?.position,
        progress: chart?.progress
    });
};
