import { types, Instance, flow, getEnv } from 'mobx-state-tree';

import {
    AccountResourceExperimentsDetails,
    AccountResourceExperimentsDetail
} from '@yandex-music-int/resource-collection';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';

const Experiment = types.model('Experiment', {
    group: types.maybe(types.string),
    value: types.maybe(types.frozen())
});

export const Experiments = types
    .model('Experiments', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        experiments: types.optional(types.map(Experiment), {})
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* (): Generator<
                Promise<AccountResourceExperimentsDetails>,
                void,
                AccountResourceExperimentsDetails
            > {
                const { accountResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield accountResource.experimentsDetails();

                    Object.entries(data).forEach(
                        ([experiment, experimentValue]: [string, AccountResourceExperimentsDetail]) => {
                            self.experiments.set(experiment, experimentValue);
                        }
                    );

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            })
        };
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        getExperiment(name: string) {
            return self.experiments.get(name);
        },
        checkExperiment(name: string, value: string) {
            const experiment = self.experiments.get(name);

            return experiment?.group === value;
        }
    }));

export type ExperimentsModel = Instance<typeof Experiments>;
