import { Instance, types } from 'mobx-state-tree';

export const CaseForms = types.model('CaseForms', {
    nominative: types.optional(types.string, ''),
    genitive: types.optional(types.string, ''),
    dative: types.optional(types.string, ''),
    accusative: types.optional(types.string, ''),
    instrumental: types.optional(types.string, ''),
    prepositional: types.optional(types.string, '')
});

export type CaseFormsModel = Instance<typeof CaseForms>;
