import { types, Instance } from 'mobx-state-tree';

import { ChartProgress } from '@yandex-music-int/resource-collection';

import { TrackWithArtists } from './TrackWithArtists.js';

export const ChartTrackWithArtists = TrackWithArtists.props({
    position: types.maybe(types.number),
    progress: types.maybe(types.enumeration(Object.values(ChartProgress)))
});

export type ChartTrackWithArtistsModel = Instance<typeof ChartTrackWithArtists>;
