import { types, Instance, getRoot, flow } from 'mobx-state-tree';

import { AlbumType, UsersResourceLibraryResponse } from '@yandex-music-int/resource-collection';

import { LikesCount } from '@/shared/models/index.js';

export const Album = LikesCount.props({
    id: types.number,
    title: types.string,
    type: types.maybe(types.enumeration(Object.values(AlbumType))),
    coverUri: types.maybe(types.string),
    averageColor: types.maybe(types.string),
    hslAverageColor: types.maybe(types.string),
    year: types.maybe(types.number),
    version: types.maybe(types.string),
    contentWarning: types.maybe(types.string),
    genre: types.maybe(types.string),
    available: types.optional(types.boolean, true)
})
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isLiked() {
            // @ts-ignore
            const { library } = getRoot(self);

            return library.isAlbumLiked(self.id);
        },
        get isAvailable() {
            // В дальнейшем тут может быть проверка на плюсовость, букмейтовость и т.д.
            return self.available !== false;
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => ({
        toggleLike: flow(function* () {
            // @ts-ignore
            const { library, user } = getRoot(self);

            if (user.isAuthorized) {
                const result = yield library.toggleAlbumLike({
                    entityId: self.id,
                    userId: user.account.uid
                });

                if (result === UsersResourceLibraryResponse.OK) {
                    if (self.isLiked) {
                        self.likePending();
                    } else {
                        self.unlikePending();
                    }
                }

                return result;
            }
        })
    }));

export type AlbumModel = Instance<typeof Album>;
