import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';
import { FormattedMessage } from 'react-intl';

import { ExplicitMark } from '@yandex-music-int/sol/components/ExplicitMark';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { ArtistModel, SeparatedArtists } from '@/entities/artist/index.js';

import { WithClassName } from '@/shared/lib/index.js';

import { getFilteredTrackAlbumArtists } from '../../../../lib/getFilteredTrackAlbumArtists.js';
import { TrackWithArtistsModel } from '../../../../model/TrackWithArtists.js';
import commonMetaStyles from '../Meta.module.scss';

import styles from './RowMeta.module.scss';

interface Props extends WithClassName {
    track: TrackWithArtistsModel;
    albumArtists?: ArtistModel[];
}

export const RowMeta = ({ track, albumArtists, className }: Props): ReactElement => {
    const disabledStyles = useMemo(() => {
        return {
            [commonMetaStyles.text_disabled]: !track.isAvailable,
            [commonMetaStyles.text_disliked]: track.isDisliked
        };
    }, [track]);

    const title = useMemo(() => {
        if (track.isRemoved) {
            return <FormattedMessage id="track-title.error-not-found" />;
        }
        return track.title;
    }, [track.isRemoved, track.title]);

    const artists = useMemo(
        () => getFilteredTrackAlbumArtists(track.artists, albumArtists),
        [track.artists, albumArtists]
    );

    return (
        <div className={clsx(styles.root, className)}>
            <Caption
                className={clsx(commonMetaStyles.text, commonMetaStyles.title, disabledStyles)}
                type="entity"
                size="m"
                weight="medium"
                variant="div"
                lineClamp={1}
            >
                {title}
            </Caption>
            {artists.length > 0 && (
                <SeparatedArtists
                    className={clsx(commonMetaStyles.text, styles.artists, disabledStyles)}
                    artists={artists}
                    lineClamp={1}
                    withLink={!track.isUGC}
                    linkClassName={clsx(commonMetaStyles.text, commonMetaStyles.artist, disabledStyles)}
                />
            )}
            {track.explicitMarkVariant && (
                <div className={commonMetaStyles.explicitMarkWrapper}>
                    <ExplicitMark variant={track.explicitMarkVariant} className={commonMetaStyles.explicitMark} />
                </div>
            )}
        </div>
    );
};
