import { cast } from 'mobx-state-tree';

import { Album } from '@yandex-music-int/resource-collection';

import { getSafeAverageColor } from '@/shared/lib/index.js';

import { type AlbumModel } from '../model/Album.js';

export const albumResponseToModel = (response: Album): AlbumModel => {
    return cast({
        id: response.id,
        title: response.title,
        coverUri: response.coverUri,
        type: response.type,
        year: response.year,
        version: response.version,
        contentWarning: response.contentWarning,
        genre: response.genre,
        likesCount: response.likesCount,
        averageColor: getSafeAverageColor(response?.derivedColors),
        available: Boolean(response.available)
    });
};
