'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo } from 'react';

import { ChartProgress } from '@yandex-music-int/resource-collection';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';

import { ChartTrackWithArtistsModel } from '../../../../model/ChartTrackWithArtists.js';

import styles from './Chart.module.scss';

interface Props extends WithClassName {
    track: ChartTrackWithArtistsModel;
}

export const Chart = observer(({ track, className }: Props): ReactElement => {
    const chartVariant = useMemo(() => {
        switch (track.progress) {
            case ChartProgress.UP:
                return 'chartUp';

            case ChartProgress.DOWN:
                return 'chartDown';

            case ChartProgress.NEW:
                return 'chartNew';

            default:
                return 'chartSame';
        }
    }, [track.progress]);

    return (
        <div className={clsx(styles.root, className)}>
            <Caption
                variant="div"
                weight="normal"
                type="entity"
                size="m"
                className={clsx(styles.position, {
                    [styles.position_disliked]: track.isDisliked,
                    [styles.position_disabled]: !track.isAvailable
                })}
            >
                {track.position}
            </Caption>
            <Icon
                variant={chartVariant}
                size="xxs"
                className={clsx(styles.progress, styles[`progress_${track.progress}`], {
                    [styles.progress_disliked]: track.isDisliked,
                    [styles.progress_disabled]: !track.isAvailable
                })}
            />
        </div>
    );
});
