import { cast } from 'mobx-state-tree';

import { Track as TrackResponse, Artist as ArtistResponse } from '@yandex-music-int/resource-collection';

import { artistResponseToModel } from '@/entities/artist/index.js';
import { trackResponseToModel } from '@/entities/track/index.js';

import { TrackWithArtistsModel } from '../model/TrackWithArtists.js';

export const trackWithArtistsResponseToModel = (trackWithArtistsResponse: TrackResponse): TrackWithArtistsModel => {
    const artists = trackWithArtistsResponse.artists?.map((artist: ArtistResponse) => {
        return artistResponseToModel(artist);
    });

    return cast({
        ...trackResponseToModel(trackWithArtistsResponse),
        artists
    });
};
