import { ReactElement } from 'react';

import { BaseNotificationLike } from '@/shared/ui/index.js';

import { PlaylistModel } from '../model/Playlist.js';

interface Props {
    playlist: PlaylistModel;
    closeToast?: () => void;
}

export const NotificationLike = ({ playlist, closeToast }: Props): ReactElement => {
    return (
        <BaseNotificationLike
            entityVariant="playlist"
            entityUrl={playlist.url}
            collectionUrl="/collection/playlists/liked"
            entityTitle={playlist.title}
            isLiked={playlist.isLiked}
            onClose={closeToast}
            coverUri={playlist.coverUri}
        />
    );
};
