import { cast } from 'mobx-state-tree';

import { UgcTrack as UgcTrackResponse } from '@yandex-music-int/resource-collection';

import { artistResponseToModel } from '@/entities/artist/index.js';
import { ugcTrackResponseToModel } from '@/entities/track/index.js';

import { UgcTrackWithArtistsModel } from '../model/UgcTrackWithArtists.js';

export const ugcTrackWithArtistsResponseToModel = (
    ugcTrackWithArtistsResponse: UgcTrackResponse | null | undefined
): UgcTrackWithArtistsModel => {
    return cast({
        ...ugcTrackResponseToModel(ugcTrackWithArtistsResponse),
        artists: ugcTrackWithArtistsResponse?.artists?.map(artistResponseToModel) ?? []
    });
};
