import { cast } from 'mobx-state-tree';

import { Playlist as PlaylistResponse } from '@yandex-music-int/resource-collection';

import { getSafeAverageColor } from '@/shared/lib/index.js';

import { type PlaylistModel } from '../model/Playlist.js';

export const playlistResponseToModel = (response: PlaylistResponse | null | undefined): PlaylistModel => {
    response = (response || {}) as PlaylistResponse;
    return cast({
        available: response.available ?? true,
        uid: response.uid,
        uuid: response.playlistUuid,
        kind: response.kind,
        title: response.title,
        //TODO: временное решение, пока задачку по склейке мазайки для обложки, на бэке не сделают
        coverUri: response?.cover?.uri || response?.cover?.itemsUri?.[0],
        likesCount: response.likesCount,
        averageColor: getSafeAverageColor(response?.derivedColors)
    });
};
