import { types, Instance, flow, getEnv } from 'mobx-state-tree';

import {
    LibraryResourceAllIdsResponse,
    UsersResourceLibraryParams,
    UsersResourceLibraryResponse,
    LibraryResourceRecordState
} from '@yandex-music-int/resource-collection';

import { LoadingState, StoreEnv } from '../lib/index.js';

enum LibraryEntityState {
    LIKED = '1',
    DISLIKED = '0'
}

type EntityId = string | number;

const EntityRecords = types.optional(types.map(types.enumeration(Object.values(LibraryEntityState))), {});

export const Library = types
    .model('Library', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        tracks: EntityRecords,
        albums: EntityRecords,
        artists: EntityRecords,
        playlists: EntityRecords
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        const setRecords = (model: Instance<typeof EntityRecords>, data: Record<number, number> | undefined): void => {
            if (!data) {
                return;
            }

            for (const id in data) {
                if (Object.prototype.hasOwnProperty.call(data, id)) {
                    const state =
                        data[id] === LibraryResourceRecordState.LIKED
                            ? LibraryEntityState.LIKED
                            : LibraryEntityState.DISLIKED;
                    model.set(String(id), state);
                }
            }
        };

        return {
            getData: flow(function* (): Generator<
                Promise<LibraryResourceAllIdsResponse>,
                void,
                LibraryResourceAllIdsResponse
            > {
                const { libraryResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield libraryResource.getAllIds({});

                    setRecords(self.tracks, data.defaultLibrary);
                    setRecords(self.albums, data.albums);
                    setRecords(self.artists, data.artists);
                    setRecords(self.playlists, data.playlists);

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            }),

            toggleTrackLike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                /*
                    Изменяем состояние Library еще до того, как запрос фактически выполнится.
                    Учитывая стабильность ручек лайка/дизлайка, это приемлемый риск, который
                    существенно улучшает отзывчивость компонентов. В случае, если запрос выполнится
                    неуспешно -- откатываем изменения в catch. При этом пользователь увидит:
                    лайк проставился->через секунду лайк снялся
                */
                const rollbackValue = self.tracks.get(entityId);

                try {
                    let response;

                    if (self.tracks.get(entityId) === LibraryEntityState.LIKED) {
                        self.tracks.delete(entityId);
                        response = yield usersResource.unlikeTrack(params);
                    } else {
                        self.tracks.set(entityId, LibraryEntityState.LIKED);
                        response = yield usersResource.likeTrack(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.tracks.set(entityId, rollbackValue);
                    } else {
                        self.tracks.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            }),

            toggleTrackDislike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                const rollbackValue = self.tracks.get(entityId);

                try {
                    let response;

                    if (self.tracks.get(entityId) === LibraryEntityState.DISLIKED) {
                        self.tracks.delete(entityId);
                        response = yield usersResource.undislikeTrack(params);
                    } else {
                        self.tracks.set(entityId, LibraryEntityState.DISLIKED);
                        response = yield usersResource.dislikeTrack(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.tracks.set(entityId, rollbackValue);
                    } else {
                        self.tracks.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            }),

            toggleArtistLike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                const rollbackValue = self.artists.get(entityId);

                try {
                    let response;

                    if (self.artists.get(entityId) === LibraryEntityState.LIKED) {
                        self.artists.delete(entityId);
                        response = yield usersResource.unlikeArtist(params);
                    } else {
                        self.artists.set(entityId, LibraryEntityState.LIKED);
                        response = yield usersResource.likeArtist(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.artists.set(entityId, rollbackValue);
                    } else {
                        self.artists.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            }),

            toggleArtistDislike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                const rollbackValue = self.artists.get(entityId);

                try {
                    let response;

                    if (self.artists.get(entityId) === LibraryEntityState.DISLIKED) {
                        self.artists.delete(entityId);
                        response = yield usersResource.undislikeArtist(params);
                    } else {
                        self.artists.set(entityId, LibraryEntityState.DISLIKED);
                        response = yield usersResource.dislikeArtist(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.artists.set(entityId, rollbackValue);
                    } else {
                        self.artists.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            }),

            toggleAlbumLike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                const rollbackValue = self.albums.get(entityId);

                try {
                    let response;

                    if (self.albums.get(entityId) === LibraryEntityState.LIKED) {
                        self.albums.delete(entityId);
                        response = yield usersResource.unlikeAlbum(params);
                    } else {
                        self.albums.set(entityId, LibraryEntityState.LIKED);
                        response = yield usersResource.likeAlbum(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.albums.set(entityId, rollbackValue);
                    } else {
                        self.albums.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            }),

            togglePlaylistLike: flow(function* (
                params: UsersResourceLibraryParams
            ): Generator<Promise<UsersResourceLibraryResponse>, void | string, UsersResourceLibraryResponse> {
                const { usersResource, logger } = getEnv<StoreEnv>(self);
                const entityId = String(params.entityId);
                const rollbackValue = self.playlists.get(entityId);

                try {
                    let response;

                    if (self.playlists.get(entityId) === LibraryEntityState.LIKED) {
                        self.playlists.delete(entityId);
                        response = yield usersResource.unlikePlaylist(params);
                    } else {
                        self.playlists.set(entityId, LibraryEntityState.LIKED);
                        response = yield usersResource.likePlaylist(params);
                    }

                    return response;
                } catch (error) {
                    if (rollbackValue) {
                        self.playlists.set(entityId, rollbackValue);
                    } else {
                        self.playlists.delete(entityId);
                    }

                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    return UsersResourceLibraryResponse.ERROR;
                }
            })
        };
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            isTrackLiked: (entityId: EntityId) => self.tracks.get(String(entityId)) === LibraryEntityState.LIKED,
            isTrackDisliked: (entityId: EntityId) => self.tracks.get(String(entityId)) === LibraryEntityState.DISLIKED,
            isArtistLiked: (entityId: EntityId) => self.artists.get(String(entityId)) === LibraryEntityState.LIKED,
            isArtistDisliked: (entityId: EntityId) =>
                self.artists.get(String(entityId)) === LibraryEntityState.DISLIKED,
            isAlbumLiked: (entityId: EntityId) => self.albums.get(String(entityId)) === LibraryEntityState.LIKED,
            isPlaylistLiked: (entityId: EntityId) => self.playlists.get(String(entityId)) === LibraryEntityState.LIKED
        };
    });

export type LibraryModel = Instance<typeof Library>;
