'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, ReactNode, useCallback } from 'react';

import { YaMusicContextData } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { ArtistModel } from '@/entities/artist/index.js';

import { WithClassName } from '@/shared/lib/index.js';

import { TrackWithArtistsModel } from '../../../model/TrackWithArtists.js';
import { ControlsBar } from '../../Cells/Controls/ControlsBar/ControlsBar.js';
import { RowMeta } from '../../Cells/Meta/RowMeta/RowMeta.js';
import { PlayButtonWithPosition } from '../../Cells/PlayButton/PlayButtonWithPosition/PlayButtonWithPosition.js';
import { ExternalPlayButtonCellProps } from '../../Cells/PlayButton/types/PlayButtonCellProps.js';
import { Track } from '../../Track/Track.js';

import styles from './TrackAlbum.module.scss';

export interface TrackAlbumProps extends WithClassName {
    track: TrackWithArtistsModel;
    /*
        Пропс нужен для фильтрации артистов у трека при отображении:
        у трека не будут отображаться те исполнители, которые есть в этом пропсе.
        Не передавайте этот пропс, если фильтрация не нужна
    */
    albumArtists?: ArtistModel[];
    position?: number;
    playContextParams: PlayContextParams<YaMusicContextData, MusicEntityData>;
}

export const TrackAlbum = observer(
    ({ track, albumArtists, position, playContextParams }: TrackAlbumProps): ReactElement => {
        const playButtonCellRender = useCallback(
            (props: ExternalPlayButtonCellProps): ReactNode => {
                return (
                    <PlayButtonWithPosition
                        track={track}
                        position={position}
                        className={styles.playButtonCell}
                        {...props}
                    />
                );
            },
            [track, position]
        );

        return (
            <Track
                track={track}
                meta={<RowMeta track={track} albumArtists={albumArtists} />}
                playButtonCellRender={playButtonCellRender}
                controls={<ControlsBar track={track} className={styles.controlsBarCell} />}
                playContextParams={playContextParams}
            />
        );
    }
);
