import { isEqual } from 'lodash-es';

import { ArtistModel } from '@/entities/artist/index.js';

// Возвращает список исполнителей для отображения в вариации Track/Album
export const getFilteredTrackAlbumArtists = (
    trackArtists: ArtistModel[] = [],
    albumArtists: ArtistModel[] = []
): ArtistModel[] => {
    if (trackArtists[0]?.decomposed?.length) {
        return trackArtists;
    }

    const albumArtistsId = albumArtists.map((artist: ArtistModel) => artist.id).sort();
    const trackArtistsId = trackArtists.map((artist: ArtistModel) => artist.id).sort();

    if (isEqual(albumArtistsId, trackArtistsId)) {
        return [];
    }

    return trackArtists.filter((artist: ArtistModel) => !artist.various);
};
