import { Instance, types } from 'mobx-state-tree';

export const LikesCount = types
    .model('LikesCount', {
        likesCount: types.maybe(types.number),
        pendingLikesCount: types.optional(types.number, 0)
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get actualLikesCount(): number {
            if (typeof self.likesCount === 'number') {
                return self.likesCount + (self.pendingLikesCount ?? 0);
            }

            return 0;
        }
    }))
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            likePending() {
                self.pendingLikesCount += 1;
            },

            unlikePending() {
                self.pendingLikesCount -= 1;
            }
        };
    });

export type LikesCountModel = Instance<typeof LikesCount>;
